@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700;800&family=Nunito&display=swap);
body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.starter{font-size:18px}

.container{margin:0 auto;max-width:1024px;padding:0 20px}.container__header{padding:0 20px}@media (min-width: 767px){.container__header{padding:0 35px}}@media (min-width: 1023px){.container__header{padding:0 55px}}.header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;grid-gap:15px;gap:15px;height:60px}.header__buttons{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;grid-gap:15px;gap:15px}.button{width:100px;height:34px;border-radius:16px;cursor:pointer;background-color:#F4E041;border:0;font-size:16px;line-height:26px}.button:hover:not(.button__invalid){background-color:#FFE302}

.container__infoSection{margin:auto;height:500px;margin-bottom:60px;background-image:url(../../static/media/BG_image_infoSection.7d8a0247.svg);background-size:cover;background-position:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@media (min-width: 1023px){.container__infoSection{height:650px}}.infoSection{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;text-align:center;grid-gap:35px;gap:35px;color:#fff}.infoSection__textInfo{font-size:16px}

.container__cardCatalog{padding:0 20px;grid-gap:35px;gap:35px;margin-bottom:60px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.cardCatalog__header{text-align:center;width:320px}@media (min-width: 767px){.cardCatalog__header{width:100%}}.card__container{display:grid;grid-gap:20px;gap:20px;grid-template-columns:repeat(1, 1fr)}@media (min-width: 767px){.card__container{grid-template-columns:repeat(2, 1fr)}}@media (min-width: 1023px){.card__container{grid-template-columns:repeat(3, 1fr)}}.cardCatalog{padding-left:20px}.cardCatalog__card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;grid-gap:25px;gap:25px;padding:20px;background-color:#fff;width:328px;cursor:pointer;transition:box-shadow 0.5s ease, -webkit-transform 0.3s ease;transition:box-shadow 0.5s ease, transform 0.3s ease;transition:box-shadow 0.5s ease, transform 0.3s ease, -webkit-transform 0.3s ease}.cardCatalog__card:hover{-webkit-transform:scale(1.1);transform:scale(1.1);box-shadow:5px 5px 10px rgba(0,0,0,0.2),-5px 5px 10px rgba(0,0,0,0.2),0 5px 10px rgba(0,0,0,0.2)}.cardCatalog__card__photo{transition:-webkit-transform 0.5s ease;transition:transform 0.5s ease;transition:transform 0.5s ease, -webkit-transform 0.5s ease}.cardCatalog__card__photo:hover{-webkit-transform:scale(1.25);transform:scale(1.25)}@media (min-width: 767px){.cardCatalog__card{width:344px}}@media (min-width: 1023px){.cardCatalog__card{width:282px}}.cardCatalog__card__photo{border-radius:50%}.cardCatalog__card__info{text-align:center}@-webkit-keyframes rotateCounterClockwise{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(-360deg);transform:rotate(-360deg)}}@keyframes rotateCounterClockwise{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(-360deg);transform:rotate(-360deg)}}.loader__img{-webkit-animation:rotateCounterClockwise 1s infinite linear;animation:rotateCounterClockwise 1s infinite linear}

@media (min-width: 767px){.section{width:380px;margin:0 auto}}.container__formSection{padding:0 20px;margin:auto;grid-gap:35px;gap:35px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.formSection{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:20px;gap:20px;width:100%}.formSection__text{text-align:center;margin-bottom:40px}

.form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:40px;gap:40px}@media (min-width: 767px){.form{-webkit-align-items:center;align-items:center}}.form__fieldset{position:relative}.form__fieldset-name{position:absolute;top:0;font-size:12px;line-height:1.5;margin:-9px 0 0 -0.5rem;margin-left:20px;background:linear-gradient(to bottom, #F8F8F8 60%, transparent 40%);padding:0 1px}.form__fieldset-name:checked{display:block}.form__field{width:100%;max-width:767px;cursor:pointer;transition:border-color 0.3s;height:54px;border:1px solid #D0CFCF;border-radius:4px;padding-left:10px}.form__field:hover{border-color:#BAB9B9}.form__field.error{border-color:#CB3D40}.form__field:focus{outline:none}@media (min-width: 767px){.form__field{width:380px}}.form__field::-webkit-input-placeholder{padding-left:10px}.form__field:-ms-input-placeholder{padding-left:10px}.form__field::placeholder{padding-left:10px}.form__field:focus::-webkit-input-placeholder{opacity:0}.form__field:focus:-ms-input-placeholder{opacity:0}.form__field:focus::placeholder{opacity:0}.form__field__example{padding-top:5px;padding-left:10px;font-size:12px;line-height:14px;color:#7E7E7E}.form__error{padding-left:12px;color:#CB3D40}.form__error-field{border-color:#CB3D40}

.radio-info{margin-bottom:10px}.radio-button{display:none}.radio-button--name{padding-left:30px}.radio-section{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:10px;gap:10px;margin-bottom:20px;transition:background 0.3s}.radio-section__field{position:relative;cursor:pointer}.radio-section__field label{position:relative;padding-left:28px;cursor:pointer;line-height:20px;display:inline-block;color:#666}.radio-section__field input[type="radio"]+p:before{content:'';position:absolute;left:0;top:0;width:20px;height:20px;border:1px solid #D0CFCF;border-radius:100%;background:#fff}.radio-section__field input[type="radio"]:checked+p:before{background:#F8F8F8}.radio-section__field input[type="radio"]:checked+p:before:hover{background:#BAB9B9}.radio-section__field input[type="radio"]:checked+p:after{content:'';width:12px;height:12px;background:#00BDD3;position:absolute;top:5px;left:5px;border-radius:100%;transition:all 0.2s ease}.radio-section__field input[type="radio"]:not(:checked)+p:after{opacity:0;-webkit-transform:scale(0);transform:scale(0)}.radio-section__field input[type="radio"]:checked+p:after{opacity:1;-webkit-transform:scale(1);transform:scale(1)}[data-title]:hover:after{opacity:1;transition:all 0.1s ease 0.5s;visibility:visible}[data-title]:after{content:attr(data-title);background-color:#000;color:#fff;position:absolute;padding:1px 5px 2px 5px;bottom:-1.6em;left:25%;border-radius:4px;opacity:0;z-index:1;visibility:hidden}[data-title]{position:relative}

.uploadImgInput--label{display:-webkit-flex;display:flex;cursor:pointer;margin:0 auto;width:100%;max-width:767px;margin-bottom:40px;cursor:pointer;height:54px;border:1px solid #D0CFCF;border-radius:4px}@media (min-width: 767px){.uploadImgInput--label{width:380px}}.uploadImgInput__button{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:0 20px;border-radius:4px 0 0 4px;border:1px solid #000}.uploadImgInput__field{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding-left:20px;width:100%;color:#7E7E7E;transition:border-color 0.3s}.uploadImgInput__field:hover{border-color:#BAB9B9}.input{display:none}.file-selected{color:#000}

.button__form{margin:0 auto;margin-bottom:60px}.button__valid{background-color:#F4E041;color:#000}.button__invalid{background-color:#B4B4B4;color:#fff}

.success__section{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;grid-gap:40px;gap:40px;margin-bottom:40px}.success__section--header{text-align:center}

body,h1,h2,h3,h4,h5,h6,p,ul,ol,li{margin:0}ul,ol{padding:0;list-style:none}a{text-decoration:none}*{box-sizing:border-box}body{font-family:"Nunito";font-weight:400;font-size:16px;line-height:26px;background-color:#F8F8F8}h1{font-size:40px;line-height:40px}

